.ga-chakra-switch{
    vertical-align: middle;
    float: right;
    text-align: center;
    width: 50px;
}

.ga-strategy-div {
    display: flex;
}

.ga-strategy-label {
    float: left;
    text-align: center;
}

input[type="radio"] {
    width: 100px;
}

.gaconfiguration-wrapper {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-gap: 5px;
    grid-auto-rows: minmax(70px, auto);
    width: 100%;    
}
  
.gaconfiguration-one {
    grid-column: 1;
    grid-row: 1;
    display: flex;
    align-items: center;
}

.gaconfiguration-two {
    grid-column: 2;
    grid-row: 1;
    display: flex;
    align-items: center;
}

.gaconfiguration-three {
    grid-column: 3;
    grid-row: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}